import React, { useState } from "react";
import ReactDOM from "react-dom";

import "./styles.css";

const days = [
  { id: 1, name: "1 day" },
  { id: 2, name: "7 days" },
  { id: 3, name: "30 days" },
  { id: 4, name: "1 year" },
  { id: 5, name: "5 year" }
];

const tiers = [
  { id: 1, name: "Tier 1" },
  { id: 2, name: "Tier 2" },
  { id: 3, name: "Tier 3" },
  { id: 4, name: "Tier 4" },
  { id: 5, name: "Tier 5" }
];

function App() {
  const [isHide, setHide] = useState(false);
  const [activeDay, setActiveDay] = useState(1);
  const [activetier, setActivetier] = useState(1);
  const [modal, setModal] = useState(true);

  const toggleHide = () => {
    setHide((prev) => !prev);
  };

  const handleDays = (id) => {
    setActiveDay(id);
  };

  const handleTier = (id) => {
    setActivetier(id);
  };

  return (
    <div className="App">
      <div className="overlay">
        <div className="modal-container">
          <div className="title-container">
            <p className="title">ROI CALCULATOR</p>
            <p
              onClick={() => {
                setModal(false);
              }}
            >
              x
            </p>
          </div>
          <div className="field-container">
            <div className="field-upper-section">
              <div className="icons-wrapper">
                <div className="icon-container">
                  <img src="https://cdn-icons-png.flaticon.com/512/276/276033.png" />
                  <p className="icon-text">CAKE</p>
                </div>
                <div className="icon-container">
                  <img src="https://cdn-icons-png.flaticon.com/512/276/276033.png" />
                  <p className="icon-text">USD</p>
                </div>
              </div>
            </div>
            <input type="text" value="0.000" disabled />
            <div className="field-lower-section">
              <p className="lower-text">~CAKE 0.000</p>
              <div className="price-tag-continer">
                <div className="price-tag">$1000</div>
                <div className="price-tag">$100</div>
              </div>
            </div>
          </div>
          <p className="item-header">Timeframe</p>
          <div className="item-container">
            {days.map((item) => (
              <div
                className={`card ${activeDay === item.id && "active"}`}
                onClick={() => handleDays(item.id)}
              >
                {item.name}
              </div>
            ))}
          </div>
          <div className="enable-wrapper">
            <p>Enable accelerated APY</p>
            <img src="https://cdn-icons-png.flaticon.com/512/276/276033.png" />
          </div>
          <p className="item-header">Select tier</p>
          <div className="item-container">
            {tiers.map((item) => (
              <div
                className={`card ${activetier === item.id && "active"}`}
                onClick={() => handleTier(item.id)}
              >
                {item.name}
              </div>
            ))}
          </div>
          <div className="field-container">
            <div className="field-upper-section">ROI at current rates</div>
            <input type="text" value="0.00" disabled />
            <div className="field-lower-section">
              <p className="lower-text">~0.000 CAKE +0.000000 DON</p>
            </div>
          </div>
          <div className="hide-text-wrapper" onClick={toggleHide}>
            <p className="hide-text"> Hide Details</p>
            <p className="arrow">{"^"}</p>
          </div>
          {!isHide && (
            <>
              <div className="list-header">
                <p className="left-text">APY</p>
                <p className="right-text">9.0%</p>
              </div>
              <ul>
                <li style={{ margin: "5px" }}>
                  Calculated based on current rates
                </li>
                <li style={{ margin: "5px" }}>
                  All figures are estimates provided for your convience only,and
                  by no means return guarantee return
                </li>
              </ul>
            </>
          )}
        </div>
      </div>
    </div>
  );
}

const rootElement = document.getElementById("root");
ReactDOM.render(<App />, rootElement);
